"use client";

import { useState, useEffect } from "react";
import { Input } from "@/components/ui/input";
import { Textarea } from "@/components/ui/textarea";
import { Button } from "@/components/ui/button";
import { Card } from "@/components/ui/card";
import { Progress } from "@/components/ui/progress";
import {
  LineChart,
  Line,
  XAxis,
  YAxis,
  CartesianGrid,
  Tooltip,
  ResponsiveContainer,
} from "recharts";

type CheckHistory = {
  emailText: string;
  url: string;
  verdict: string;
  confidence: number;
  signals: string[];
  timestamp: string;
};

export default function Home() {
  const [emailText, setEmailText] = useState("");
  const [url, setUrl] = useState("");
  const [loading, setLoading] = useState(false);
  const [history, setHistory] = useState<CheckHistory[]>([]);
  const [lastResult, setLastResult] = useState<CheckHistory | null>(null);
  const [isMounted, setIsMounted] = useState(false);

  useEffect(() => {
    setIsMounted(true);
    const stored = localStorage.getItem("phishingHistory");
    if (stored) setHistory(JSON.parse(stored));
  }, []);

  useEffect(() => {
    if (isMounted) localStorage.setItem("phishingHistory", JSON.stringify(history));
  }, [history, isMounted]);

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);

    const res = await fetch("/api/detect", {
      method: "POST",
      headers: { "Content-Type": "application/json" },
      body: JSON.stringify({ emailText, url }),
    });

    const data = await res.json();
    setLoading(false);

    const newCheck: CheckHistory = {
      emailText,
      url,
      ...data,
      timestamp: new Date().toLocaleString(),
    };

    setHistory((prev) => [newCheck, ...prev]);
    setLastResult(newCheck);

    localStorage.setItem("lastResult", JSON.stringify(newCheck));
  };

  const clearHistory = () => {
    setHistory([]);
    localStorage.removeItem("phishingHistory");
  };

  if (!isMounted) return null;

  return (
    <div className="flex flex-col items-center justify-center min-h-screen p-6 bg-gray-50">
      <h1 className="text-3xl font-bold mb-6">Phishing Detection</h1>

      {/* Form */}
      <form onSubmit={handleSubmit} className="w-full max-w-xl space-y-4">
        <Textarea
          placeholder="Paste email content here..."
          value={emailText}
          onChange={(e) => setEmailText(e.target.value)}
          className="resize-none"
          rows={6}
        />
        <Input
          placeholder="Or enter a URL to check"
          value={url}
          onChange={(e) => setUrl(e.target.value)}
        />
        <Button type="submit" disabled={loading}>
          {loading ? "Checking..." : "Check for Phishing"}
        </Button>
      </form>

      {/* Latest Result */}
      {lastResult && (
        <Card
          className={`p-6 w-full max-w-xl mt-6 ${
            lastResult.verdict === "phishing" ? "bg-red-200" : "bg-green-200"
          }`}
        >
          <p className="text-sm text-gray-500 mb-2">{lastResult.timestamp}</p>
          <h2 className="text-2xl font-semibold mb-2 capitalize">{lastResult.verdict}</h2>
          <p className="mb-4">Confidence: {lastResult.confidence}%</p>
          <Progress value={lastResult.confidence} className="mb-4" />
          {lastResult.signals.length > 0 && (
            <>
              <h3 className="font-semibold mb-2">Triggered Signals:</h3>
              <ul className="list-disc list-inside">
                {lastResult.signals.map((s, i) => (
                  <li key={i}>{s}</li>
                ))}
              </ul>
            </>
          )}
        </Card>
      )}

      {/* Chart */}
      {history.length > 1 && (
        <div className="w-full max-w-xl mt-10 h-64">
          <h2 className="text-2xl font-semibold mb-4">Confidence Trend</h2>
          <ResponsiveContainer width="100%" height="100%">
            <LineChart data={[...history].reverse()}>
              <CartesianGrid strokeDasharray="3 3" />
              <XAxis dataKey="timestamp" tick={{ fontSize: 12 }} />
              <YAxis domain={[0, 100]} />
              <Tooltip />
              <Line
                type="monotone"
                dataKey="confidence"
                stroke="#2563EB"
                strokeWidth={2}
                dot={{ r: 4 }}
              />
            </LineChart>
          </ResponsiveContainer>
        </div>
      )}

      {/* History */}
      {history.length > 0 && (
        <div className="mt-10 w-full max-w-xl space-y-4">
          <div className="flex justify-between items-center">
            <h2 className="text-2xl font-semibold mb-4">Check History</h2>
            <Button variant="destructive" size="sm" onClick={clearHistory}>
              Clear History
            </Button>
          </div>

          {history.map((item, idx) => (
            <Card key={idx} className="p-4">
              <p className="text-sm text-gray-500">{item.timestamp}</p>
              <p><strong>Email:</strong> {item.emailText || "—"}</p>
              <p><strong>URL:</strong> {item.url || "—"}</p>
              <p>
                <strong>Verdict:</strong>{" "}
                <span className={item.verdict === "phishing" ? "text-red-600" : "text-green-600"}>
                  {item.verdict}
                </span>
              </p>
              <p><strong>Confidence:</strong> {item.confidence}%</p>
              {item.signals.length > 0 && (
                <p><strong>Signals:</strong> {item.signals.join(", ")}</p>
              )}
            </Card>
          ))}
        </div>
      )}
    </div>
  );
}
